@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body{
    font-family: 'Roboto', sans-serif;
    /* display: none; */
}
nav{
    margin: 15px;
}
.navbar-brand{
    padding: 0px 25px;
    margin: -4px 0 0 0;
}
.logo{
    /* border: 1px solid red; */
    height: 40px;
    width: 14vw;
    background-image: url(/static/media/logo.cc3cc3ae.jpg);
    background-repeat: no-repeat, repeat;
    background-size: cover;
    background-position: center;
}
.list-container{
    margin: 0px 0px 0px 35vw;
}

.login-container{
    width: 50%;
    margin-top: 80px;
}
.login-text{
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
}
form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
form > input{
    border-radius: 13px;
}

.btn:focus,.btn:active {
    outline: none !important;
    box-shadow: none;
 }
 .shadow-none{
     border-radius: 10px;
 }

.login-text{
    font-weight: 700;
}
.forgot-pwd-text{
    font-size: 15px;
    text-decoration: underline;
    color: #e83e8cd9;
}


.btn-logIn, .btn-signUp{
    width: 9vw;
    height: 30px;
    border-radius: 14px;
    font-size: 13px;
    padding: 0px; 
}
.nav-link{
    font-size: 15px;
    padding: 12px;
}
.btn-signUp{
    width: 9vw;
    height: 30px;
    border-radius: 14px;
    font-size: 13px;
    padding: 0px; 
    border: none;
    outline: none;
    background-image: -webkit-linear-gradient(150deg, #ff5858 0%, #f09819 100%);
    background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
    margin: -4px 0 0 0;
}
.btn-signOut{
    width: 9vw;
    height: 30px;
    border-radius: 20px;
    font-size: 13px;
    padding: 0px; 
    border: none;
    outline: none;
    margin: -4px 0 0 0;
}
.btn-logIn{
    width: 9vw;
    height: 30px;
    border-radius: 14px;
    font-size: 13px;
    padding: 0px; 
    border: none;
    outline: none;
    margin: -4px 0 0 0;
}


input{
    outline: none;
    border-radius: 10px;
}
.btn-login{
    background-image: -webkit-linear-gradient(150deg, #ff5858 0%, #f09819 100%);
    background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
    border: none;
    border-radius: 10px;
}
.btn-signOut{
    background: #dc3545;
    color: white;
    border: none;
    border-radius: 14px;
}
.not-a-member {
    font-size: 15px;
}

.not-a-member > div{
    padding: 0px 10px;
}
.not-a-member > div:nth-child(2){
    color: #f09819;
}





@media screen and (max-width: 990px) {
   .login-container{
       width: 100%;
   }
   .btn-google, .btn-facebook{
    width: 40%;
   }
   .list-container{
    margin: 0px;
}
.btn-logIn, .btn-signUp{
    width: 40%;
}
.logo{
    width: 45vw;
    -webkit-transform: translateX(-28px);
            transform: translateX(-28px);
}

}
.left-div{
    /* border: 1px solid red; */
}

.right-div{
    /* border: 1px solid green; */
}

.img-div{
    border-radius: 95px;
    border: 1px solid black;
    height: 30vh;
    width: 14vw;
    margin: 50px 0px 30px 0px;
}

.top-right-div {
    /* border: 1px solid red; */
    width: 85%;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 60px 0px;

}
.top-right-div > ul{
    list-style-type: none;
}
.list-title{
    font-weight: 800;
    font-size: 17px;
}

.list-desc{

}

.bottom-right-div{
    /* border: 1px solid red; */
    width: 85%;
}

.staff-photo{
    border-radius: 50%;
    /* border: 1px solid red; */
    /* height: 50%; */
    width: 100%;
}
.action-btn{
    border-radius: 50px; 
}

.add-staff-btn{
    -webkit-transform: translateY(81px);
            transform: translateY(81px);
}
